<template>
  <v-container>
    <v-layout wrap>
      <v-card class="full-width mb-3 elevation-1">
        <v-card-actions>
            <v-btn @click="generateCircles">1. Add Some Circles</v-btn>
            <v-btn @click="toggleAnimation">2. {{ running ? 'Stop Animations' : 'Start Animations' }}</v-btn>
            <v-btn @click="reset">3. Reset</v-btn>
        </v-card-actions>
      </v-card>
      <v-card class="full-width mb-3 elevation-1">
        <v-card-text>
          <l-map
            ref="map"
            :center.sync="center"
            :zoom.sync="zoom"
            :style="{height: '600px'}"
          >
            <l-tile-layer :url="url" :attribution="attribution"/>
          </l-map>
        </v-card-text>
      </v-card>
    </v-layout>
  </v-container>
</template>

<script>
import { latLng, svgOverlay } from 'leaflet'
import geometryUtil from 'leaflet-geometryutil'
import { LMap, LTileLayer, LCircle } from 'vue2-leaflet'

export default {
  components: {
    LMap, LTileLayer, LCircle
  },
  data () {
    return {
      center: latLng(10, 10),
      zoom: 7,
      url: 'http://{s}.tile.osm.org/{z}/{x}/{y}.png',
      attribution: '<a href="http://osm.org/copyright">OpenStreetMap</a>',
      bounds: null,
      map: null,
      mapReady: false,
      circles: [],
      circlesCountToGenerate: 10,
      running: false
    }
  },
  mounted () {
    this.$nextTick(() => {
      this.map = this.$refs.map.mapObject
      this.mapReady = true
    })
  },
  methods: {
    render () {
      if (this.circleOverlays && this.circleOverlays.length) {
        this.circleOverlays.forEach(overlay => {
          overlay.remove()
        })
      }
      const getArcCoords = (x, y, angle, distance) => [x + Math.cos(Math.PI * angle / 180) * distance, y + Math.sin(Math.PI * angle / 180) * distance]
      this.circleOverlays = []
      this.circles.forEach(circle => {
        const e = document.createElementNS('http://www.w3.org/2000/svg', 'svg')
        e.setAttribute('xmlns', 'http://www.w3.org/2000/svg')
        e.setAttribute('viewBox', '-5 -5 210 210')
        const r = 100 * circle.radius / (circle.radius + circle.stripe)
        const arcStart = getArcCoords(100, 100, circle.direction, r)
        const arcEnd = getArcCoords(100, 100, circle.direction + circle.sectorAngle, r)
        e.innerHTML =
          `<circle cx="100" cy="100" r="100" fill="#fff" fill-opacity="0.4"/>` +
          `<circle cx="100" cy="100" r="${r}" fill="transparent" stroke="#fff" stroke-width="4" stroke-opacity="0.8"/>` +
          `<path d="M ${arcStart[0]} ${arcStart[1]} A ${r} ${r} 0 1 1 ${arcEnd[0]} ${arcEnd[1]} L 100 100 Z" stroke="#fff" fill="${circle.color}" stroke-width="4" fill-opacity="0.5" stroke-opacity="0.7"/>` +
          `<circle cx="100" cy="100" r="10" stroke="#fff" fill="#000" stroke-width="4"/>` +
          `<path d="M 100 100 L ${arcStart[0]} ${arcStart[1]}" stroke="#000" stroke-width="5" stroke-linecap="round"/>`
        this.circleOverlays.push(svgOverlay(e, [[circle.n, circle.e], [circle.s, circle.w]]).addTo(this.map))
      })
    },
    toggleAnimation () {
      this.running = !this.running
      this._runningTimeout && clearTimeout(this._runningTimeout)
      if (!this.running) return false
      const startTime = (new Date).getTime()
      const run = () => {
        const time = (new Date).getTime()
        this.circles.forEach(circle => {
          let dt = (time - startTime) % circle.duration
          let counter = 0
          let p1 = circle.track[0]
          let p2 = circle.track[1]
          let delta = 0
          let i = 0
          for (i = 1; i < circle.track.length; i++) {
            if (counter <= dt && counter + circle.track[i].dt > dt) {
              p1 = circle.track[i - 1]
              p2 = circle.track[i]
              delta = dt - counter
              break
            }
            counter += circle.track[i].dt
          }
          Object.assign(circle, {
            center: {
              lat: this.getLinear(p1.center.lat, p2.center.lat, delta / p2.dt),
              lng: this.getLinear(p1.center.lng, p2.center.lng, delta / p2.dt)
            },
            radius: this.getLinear(p1.radius, p2.radius, delta / p2.dt),
            stripe: this.getLinear(p1.stripe, p2.stripe, delta / p2.dt),
            direction: this.getLinear(p1.direction, p2.direction, delta / p2.dt),
            sectorAngle: this.getLinear(p1.sectorAngle, p2.sectorAngle, delta / p2.dt)
          })
        })
        this.render()
        this._runningTimeout = setTimeout(run, 60)
      }
      run()
    },
    generateCircles () {
      const bounds = this.map.getBounds()
      const nwPoint = bounds.getNorthWest()
      const swPoint = bounds.getSouthWest()
      const basicDistance = this.map.distance(nwPoint, swPoint) / 10
      for (let i = 0; i < this.circlesCountToGenerate; i++) {
        const circle = {
          center: {
            lat: this.getRandomBetween(bounds.getNorth(), bounds.getSouth()),
            lng: this.getRandomBetween(bounds.getWest(), bounds.getEast())
          },
          radius: this.getRandomBetween(basicDistance / 2, basicDistance),
          stripe: this.getRandomBetween(basicDistance / 10, basicDistance / 4),
          direction: this.getRandomBetween(0, 360),
          sectorAngle: -1 * this.getRandomBetween(30, 90),
          color: this.chooseRandomIn(['#999', '#900', '#369', '#090', '#009'])
        }
        circle.n = geometryUtil.destination(circle.center, 0, circle.radius + circle.stripe).lat
        circle.s = geometryUtil.destination(circle.center, 180, circle.radius + circle.stripe).lat
        circle.w = geometryUtil.destination(circle.center, -90, circle.radius + circle.stripe).lng
        circle.e = geometryUtil.destination(circle.center, 90, circle.radius + circle.stripe).lng
        circle.track = this.generateTrack(circle, basicDistance)
        circle.duration = circle.track.reduce((sum, dot) => {
          sum += dot.dt
          return sum
        }, 0)
        this.circles.push(circle)
      }
      this.render()
    },
    generateTrack (circle, basicDistance) {
      const track = []
      track.push({...circle, dt: 0})
      const numberOfPoints = Math.floor(Math.random() * 20)
      for (let i = 0; i < numberOfPoints; i++) {
        track.push({
          center: {
            lat: circle.lat + this.getRandomBetween(circle.n - circle.s, circle.s - circle.n),
            lng: circle.lng + this.getRandomBetween(circle.w - circle.e, circle.e - circle.w)
          },
          radius: this.getRandomBetween(basicDistance / 2, basicDistance),
          stripe: this.getRandomBetween(basicDistance / 10, basicDistance / 4),
          direction: this.getRandomBetween(0, 360),
          sectorAngle: -1 * this.getRandomBetween(30, 90),
          dt: 500 + Math.floor(Math.random() * 1000)
        })
      }
      track.push({...circle, dt: 500 + Math.floor(Math.random() * 1000)})
      return track
    },
    reset () {
      this.circles = []
      this.render()
      if (this.running) this.toggleAnimation()
    },
    getRandomBetween (n1, n2) {
      return n1 + (n2 - n1) * Math.random()
    },
    chooseRandomIn (ar) {
      return ar[Math.floor(Math.random() * ar.length)]
    },
    getLinear(v1, v2, p) {
      return v1 + (v2 - v1) * p
    }
  }
}
</script>
